.photo__wrapper {
  padding: 18px 20px 24px 20px;

  @media (min-width: @tablet-width) {
    padding: 0 60px 0 60px;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
  }
}

.photo__list {
  list-style-type: none;
  padding: 0;
  margin: 0;

  @media (min-width: @tablet-width) {
    display: grid;
    grid-template-columns: repeat(2, 324px);
  }

  @media (min-width: @desktop-width) {
    grid-template-columns: repeat(3, 400px);
  }
}

.photo__item {
  padding-top: 24px;
  position: relative;

  @media (min-width: @tablet-width) {
    padding: 0;
  }

  @media (min-width: @desktop-width) {
    padding: 0;
  }
}

.photo__item::after {
  content: "";
  width: 320px;
  position: absolute;
  border-bottom: 1px solid #d6d6d6;
  right: -20px;

  @media (min-width: @tablet-width) {
    position: static;
  }

  @media (min-width: @desktop-width) {
    position: static;
  }
}

.photo__item:first-child::before {
  content: "";
  width: 320px;
  position: absolute;
  border-bottom: 1px solid #d6d6d6;
  right: -20px;
  top: 1px;

  @media (min-width: @tablet-width) {
    position: static;
  }

  @media (min-width: @desktop-width) {
    position: static;
  }
}


.photo__item:first-child {
  @media (min-width: @tablet-width) {
    grid-column: 1/3;
  }

  @media (min-width: @desktop-width) {
    grid-column: 1/4;
  }
}

.photo__img {
  object-fit: cover;
  display: block;

  @media (min-width: @tablet-width) {
    width: 648px;
  }

  @media (min-width: @desktop-width) {
    width: 1200px;
  }
}

.photo__title {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 21px;
  text-transform: uppercase;
  margin: 0;
  padding: 22px 0 1px 0;
}

.photo__author {
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 21px;
  margin: 0;
}

.photo__contaiter {
  @media (min-width: @tablet-width) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
    padding: 0 20px 15px 20px;
    background: #f2f2f2;
  }
}

.photo__like {
  padding: 11px 0 25px 0;
  display: flex;
  gap: 14px;

  @media (min-width: @tablet-width) {
    gap: 9px;
  }
}
